
* {
  margin: 0;
  padding: 0;
  word-break: break-all;
  font-style: normal;
}
html, body {
  height: 100%;
}
body {
  font-size: 0.21333rem;
  font-family: PingFang SC, Microsoft YaHei;
  -webkit-tap-highlight-color: transparent;
  background: #F3F3F3;
  color: #333333;
  overflow: auto;
  left: 0;
  right: 0;
}
a {
  color: inherit;
  text-decoration: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}
a:hover {
    color: inherit;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}
ul, ol {
  list-style: none;
}
img, input, button {
  border: none;
  outline: none;
}
input, button {
  font-size: 14px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
input[type=number] {
  -webkit-appearance: textfield;
          appearance: textfield;
}
input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
    -webkit-appearance: none !important;
            appearance: none !important;
    margin: 0;
}
.xqd-ui .vux-header {
  background-color: #fff;
}
.xqd-ui .vux-header .vux-header-title {
    color: #000;
}
.button-module {
  display: block;
  font-size: 0.45333rem;
  width: 7.33333rem;
  height: 1.13333rem;
  background: #306bd0;
  border-radius: 1.32rem;
  color: #fff;
  margin: 0 auto;
  text-align: center;
  line-height: 1.13333rem;
}
.button-module:active {
    border-color: rgba(48, 107, 208, 0.6) !important;
    color: rgba(48, 107, 208, 0.6) !important;
    background-color: transparent;
}
.small-font.smallsize-font {
  font-size: 12px;
  -webkit-transform-origin-x: 0;
  -webkit-transform: scale(0.8);
}
.weui-toast.weui-toast_success {
  top: 50%;
  width: 4rem !important;
}



